.hidden {
  visibility: hidden;
  opacity: 0;
}

p {
  margin: 0;
  padding: 0;
}

.modal {

  &__wrapper {
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-self: center;


    background: rgba(41, 41, 41, 0.6);
    z-index: 111;

    font-family: 'Georgia';
    font-weight: 400;
    box-shadow: 0px 2px 35px 14px rgba(13, 13, 13, 0.04);
    color: #000000;

    transition: all 1s ease;
  }

  &__body {
    cursor: auto;
    display: flex;
    justify-content: center;
    align-self: center;
    position: relative;
    z-index: 1111;

    max-width: 74.5vw;
    background: #FAFAFA;
    border-radius: 9px;

    @media (max-width:991.98px) and (orientation: landscape) {
      margin-top: 5%;
      max-height: 75vh;
    }

    @media (max-width:768.98px) {
      max-width: 83vw;
    }

    @media (max-width:768.98px) and (orientation: landscape) {
      max-height: 80vh;
      margin-top: 7%;
    }

    @media (max-width:540.98px) {
      max-width: 88vw;
    }

    @media (max-width:499.98px) {
      max-width: 75vw;
    }


  }

  &__close--btn {
    cursor: pointer;
    position: absolute;
    right: -1.99rem;
    top: -3.3rem;
    padding-right: 5px;

    @media (max-width: 991.98px) {
      right: -3vw;
      top: -6.4vw;
    }

    @media (max-width: 768.98px) {
      right: -3.5vw;
      top: -8vw;
    }

    @media (max-width: 620.98px) {
      right: -1.9rem;
      top: -3.6rem;
    }
  }

  &__image {
    display: flex;
    justify-content: flex-start;
    width: 100%;
    flex: 1 1 50%;

    @media (max-width:991.98px) and (orientation: landscape) {
      flex: 1 1 20%;
    }

    @media (max-width:499.98px) {
      display: none;
    }

  }

  &-img {
    width: 47.8vw;
    object-fit: cover;

    @media (max-width:768.98px) {
      max-width: 45.573vw;
      max-height: 350px;
    }

    @media (max-width:991.98px) and (orientation: landscape) {}
  }

  &__text {
    display: flex;
    flex-direction: column;
    flex: 1 1 45%;
    font-size: 0.9375rem;
    line-height: 110%;

    margin: 0 0 0 6.250vw;
    padding: 5.28% 2.2% 9.47% 0; // 2.2 // 6,250vw

    @media (max-width:768.98px) {
      width: 275px;
      margin: 0.625rem 2.2% 2.3125rem 6.250vw;
      padding: 0;
    }

    @media (max-width:683.98px) {
      max-width: none;
      margin: 0.625rem 0.625rem 2% 3%;
      padding: 0;
    }

    @media (max-width:576.98px) {
      margin: 0.625rem;
    }

    @media (max-width:991.98px) and (orientation: landscape) {
      flex: 1 1 20%;
    }

    @media (max-width:991.98px) and (orientation: landscape) {
      margin: 0;
      padding: 2%;
    }
  }

  &__name {
    font-size: 2.1875rem;
    line-height: 130%;
    color: #000000;
    margin-bottom: 2.9%;
    letter-spacing: 0.06em;

    @media (max-width:499.98px) {
      text-align: center;
    }
  }

  &__dogtype {
    margin-bottom: 40px;
    font-size: 1.25rem;
    line-height: 115%;

    @media (max-width:768.98px) {
      margin-bottom: 20px;
    }

    @media (max-width:499.98px) {
      text-align: center;
    }

    @media (max-width:991.98px) and (orientation: landscape) {
      margin: 0;
      padding-bottom: 6%;
    }
  }

  &__desqr {
    // width: 27.344vw;
    font-size: 0.9375rem;
    line-height: 110%;
    margin-bottom: 40px;
    text-align: justify;
    letter-spacing: 0.06em;

    @media (max-width:768.98px) {
      margin-bottom: 20px;
      max-width: 100%;
      font-size: 0.8125rem;
      letter-spacing: 0;
    }

    @media (max-width:499.98px) {
      margin-bottom: 24px;
    }

    @media (max-width:991.98px) and (orientation: landscape) {
      margin: 0;
      padding-bottom: 6%;
    }
  }

  &__about {
    display: flex;
    align-items: start;
    flex-direction: column;
  }

  &__about--item {
    font-weight: 700;
    font-size: 0.9375rem;
    line-height: 110%;

    &:not(:last-child) {
      margin-bottom: 11px;

      @media (max-width:768.98px) {
        margin-bottom: 5px;
      }

      @media (max-width:768.98px) and (orientation: landscape) {
        margin-bottom: 10px;
      }

      @media (max-width:991.98px) and (orientation: landscape) {
        margin-bottom: 5px;
      }
    }
  }

  &__about--more {
    font-weight: 400;
  }
}
