.help {

  padding-bottom: 6.25rem;

  @media (max-width: 570.98px) {
    padding-bottom: 2rem;
  }

  &__title {
    margin-top: 5rem;
    margin-bottom: 3.75rem;

    font-family: 'Georgia';
    font-style: normal;
    font-weight: 400;
    font-size: 2.1875rem;
    line-height: 130%;
    text-align: center;
    color: #545454;

    @media (max-width: 570.98px) {
      font-size: 1.5625rem;
      margin: 2.625rem 0px;
    }
  }
  &-img{
    display: flex;
    flex: 1 1 100%;
    margin: 0 auto;
  }
  &__object--wrapper {
    display: grid;
    margin: 0;
    align-items: center;
    align-items: center;
    justify-content: center;
    text-align: center;


    column-gap: 6.406vw;
    row-gap: 3.4375rem;
    padding: 0 1.25rem;
    list-style-type: none;

    grid-template-columns: repeat(5, minmax(10%, 1fr));
    grid-template-rows: repeat(2, 1fr);

    @media (max-width: 768.98px) {
      display: grid;

      grid-template-columns: repeat(3, minmax(10%, 1fr));
    }

    @media (max-width: 570.98px) {
      row-gap: 1.875rem;
      column-gap: 1.875rem;
      grid-template-columns: repeat(2, minmax(130px, 1fr));
    }
  }


  &__objects {
    flex: 1 1 100%;

    font-family: 'Georgia';
    font-weight: 400;
    font-size: 1.25rem;
    line-height: 115%;
    color: #545454;

    @media (max-width: 570.98px) {
      font-size: 0.9375rem;
    }
  }

  &__type {

    margin: 1.875rem 0 0 0;
    @media (max-width: 570.98px) {
      margin: 1.25rem 0 0 0;
    }
  }
}

.row1 {
  margin-left: 120%;
  display: grid;
  justify-content: center;

  @media (max-width: 768.98px) {
    display: block;
    margin: 0;
  }
}
