.hero {
  background: radial-gradient(100% 215.42% at 0% 0%, #5B483A 0%, #262425 100%), #211F20;

  &__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;


    @media (max-width:768.98px) {
      flex-wrap: wrap;
      text-align: center;
      justify-content: center;
      align-items: center;
    }
  }

  &__inner {
    padding-top: 28.5%;
    padding-bottom: 30px;

    @media (max-width:991.98px) {
      padding-top: 25.5%;
    }

    @media (max-width:768.98px) {
      flex: 1 1 100%;
      padding-top: 9.375rem;
      max-width: 460px;
      padding-bottom: 0px;
    }

    @media (max-width:400.98px) {
      margin: 0;
    }

  }

  &__background {
    padding-top: 15%;

    @media (max-width:991.98px) {
      margin-top: auto;
    }

    @media (max-width:768.98px) {
      margin-left: auto;
      padding-top: 14.2%;
      flex: 1 1 100%;
      max-width: 569px;
    }
  }

  &__image {
    width: 100%;
    display: flex;
    @media (max-width:768.98px) {
      max-width: 569px;
    }
  }

  &__title {
    max-width: 19.375rem;
    margin: 0px 0px 2.625rem 0px;
    font-family: 'Georgia';
    font-weight: 400;
    font-size: 2.75rem;
    line-height: 130%;
    color: var(--light-color);

    @media (max-width:768.98px) {
      max-width: none;

    }

    @media (max-width:620.98px) {
      font-size: 2.5rem;

    }

    @media (max-width:520.98px) {
      font-size: 2.1875rem;

    }

    @media (max-width:420.98px) {
      font-size: 1.875rem;

    }

    @media (max-width:320.98px) {
      font-size: 1.5625rem;

    }
  }

  &__text {
    max-width: 28.75rem;
    margin: 0px 0px 42px 0px;
    font-family: 'Arial';
    font-weight: 400;
    font-size: 0.9375rem;
    line-height: 160%;
    color: #CDCDCD;

    @media (max-width:768.98px) {
      max-width: none;
    }
  }
}
