.burger {
  display: none;
}

.active {
  // position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background: #292929;
  visibility: visible;
  margin-right: 0;
  opacity: 1;
  transition: all 1.5s ease;

}
@media(max-width:768.98px) {

  .burger {
    cursor: pointer;
    display: block;
    z-index: 5;
    background-color: inherit;
    position: relative;
    width: 44px;
    height: 44px;
    border: none;
    border-radius: 20%;

  }

  .rotate {
    animation-name: rotate;
    animation-duration: 1.5s;
    animation-delay: 0s;
    animation-fill-mode: forwards;
  }

  @keyframes rotate {
    from {
      transform: rotate(360deg);
    }

    to {
      transform: rotate(90deg);
    }
  }

.burger.rotate{
  position: relative;
  right: 0;
}


  .rotateback {
    animation-name: rotateback;
    animation-duration: 1.5s;
    animation-delay: 0s;
    animation-fill-mode: backwards;
  }

  @keyframes rotateback {
    from {
      transform: rotate(90deg);
    }

    to {
      transform: rotate(360deg);
    }
  }

  .line,
  .line2,
  .line3 {
    background-color: #F1CDB3;
    position: absolute;
    border-radius: 10%;
    top: 50%;
    left: 15%;
    width: 30px;
    height: 2px;
  }

  .line2 {
    position: absolute;
    top: 20%;
    left: 15%;
  }

  .line3 {
    position: absolute;
    top: 80%;
    left: 15%;
  }
  .line__black{
    background-color: black;
  }
}
.rotate .line__black{
  background-color: white;
}
.whiteColors{
  color: white;
  transition: all 2s ease;
}
