.ourfriends {
  background: #F6F6F6;
  padding-top: 5rem;
  padding-bottom: 7.5%;

  &__title {
    margin: 0px 0px 3.75rem 0px;
    text-align: center;
    color: #545454;
    font-family: 'Georgia';
    font-weight: 400;
    font-size: 2.1875rem;
    line-height: 130%;
  }

  &__btn {
    display: flex;
    justify-content: center;
  }

  &__slider {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__link {
    color: inherit;
  }
}

.card {
  &__wrapper {
    overflow: hidden;
  }

  &__gridwrap {
    flex: 1 1 90%;
    flex-basis: 80%;
    position: relative;
    display: flex;
    max-width: 46.875rem;
    height: 100%;
    left: 0;
    transition: all 1s ease;
    column-gap: 5.625rem;
    @media (max-width: 1280px){
      column-gap: 7.031vw;
    }
    @media (max-width: 768.98px) {
      column-gap: 2.5rem;
        }
    @media (max-width: 991.98px) {
      max-width: 28.75rem;
    }

    @media (max-width: 600.98px) {
      column-gap: 5.208vw;
      justify-self: center;
      max-width: 13.4375rem;
      margin-bottom: 3.125rem;
    }
  }

  &__body {
    max-width: 16.875rem;
    text-align: center;
  }

  &__arrow {
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    // padding: 0.5% 0.2%;
    max-width: 3.25rem;
    height: 3.25rem;
    flex-basis: 10%;
    border: 2px solid #F1CDB3;
    border-radius: 100px;
    background-color: inherit;

    @media (max-width: 600.98px) {
      align-self: flex-end;

      position: relative;
      bottom: -3.125rem;
    }
    @media (max-width: 570.98px) {
      flex-basis: 20%;
    }
  }
  &-img {
    object-fit: cover;
    width: 100%;
  }

  &__name {
    margin: 30px 0px;
    font-family: 'Georgia';
    font-weight: 400;
    font-size: 1.25rem;
    line-height: 143.75%;
    color: #545454;

  }

  &__arrow--left {
    left: 30%;
    @media (max-width: 470.98px) {
      left: 20%;
    }
  }

  &__arrow--right {
    left: -30%;
    @media (max-width: 470.98px) {
      left: -20%;
    }
  }
}

.arrow-left,
.arrow-right {
  width: 0.875rem;
  height: 0.375rem;

}

.block {
  display: block;
  object-fit: cover;
  width: 100%;
  height: 100%;
}
