.btn {
  cursor: pointer;
  background: #F1CDB3;
  border: 0px;
  border-radius: 100px;
  padding: 0.9375rem 2.8125rem;
  border: 4px solid #f1cdb3;
  font-family: 'Georgia';
  font-weight: 400;
  font-size: 1.0625rem;
  line-height: 130%;
  letter-spacing: 0.06em;
  color: #292929;
  transition: all .3s ease-in;

  &:hover {
    background-color: inherit;
    color: rgb(0, 0, 0);

  }

  &--learnmore {
    cursor: pointer;
    border: 2px solid #F1CDB3;
    background-color: inherit;
    white-space: nowrap;

    &:hover {
      background-color: #F1CDB3;
    }
  }

  &--know {
    font-family: 'Georgia';
    font-weight: 400;
    font-size: 1.0625rem;
    line-height: 130%;
    color: #292929;
    margin-top: 50px;

    padding: 15px 45px;
    background: #F1CDB3;
    border-radius: 100px;
    text-align: center;
    display: flex;
    justify-content: center;

    @media (max-width: 600.98px) {
      margin-top: 90px;
    }
  }
}

.btns {
  cursor: pointer;
  font-family: 'Georgia';
  font-weight: 400;
  font-size: 20px;
  line-height: 115%;
  letter-spacing: 0.06em;
  color: #292929;

  display: flex;
  align-items: center;
  text-align: center;
  justify-content: center;

  background-color: inherit;

  // letter-spacing: 0.06em;
  width: 52px;
  height: 52px;
  border-radius: 100%;

  border: 2px solid #F1CDB3;

  margin-left: 10px;

  &__number{

    &:hover, &:focus{
      background-color: #F1CDB3;
      white-space: nowrap;
    }
  }
}
.btnactive{
  background-color: #F1CDB3;
}
