.footer {
  background: radial-gradient(110.67% 538.64% at 5.73% 50%, #513D2F 0%, #1A1A1C 100%), #211F20;
  padding-top: 40px;

  &__inner {
    display: flex;
    justify-content: space-between;

    @media (max-width: 996.98px) {
      flex-wrap: wrap;
    }

    @media (max-width: 768.98px) {
      justify-content: stretch;
    }
  }

  &__image {
    display: flex;
    align-self: flex-end;
    max-width: 100%;

    @media (max-width: 996.98px) {
      flex: 1 1 100%;
      justify-content: center;
    }
  }

  &-img {
    width: 300px;
  }

  &__faq {
    padding-bottom: 60px;
    margin-right: 13.35%;

    @media (max-width: 996.98px) {
      margin-right: 3.75rem;
      flex: 1 1 30%;
    }

    @media (max-width: 570.98px) {
      flex: 1 1 100%;
    }

    @media (max-width: 570.98px) {
      flex: 1 1 100%;
      justify-content: center;
      margin: 0 0 40px 0;
      padding: 0;
      text-align: center;
    }
  }

  &__location {
    padding-bottom: 60px;
    margin-right: 13.35%;

    @media (max-width: 996.98px) {
      margin-right: 2.125rem;
      flex: 1 1 30%;
    }

    @media (max-width: 768.98px) {
      margin-right: 0;
    }

    @media (max-width: 570.98px) {
      flex: 1 1 100%;
      margin: 0 auto 40px auto;
      padding: 0;
      text-align: center;
    }
  }

  &__contacts {
    display: inline-block;

  }

  &__contacts--title {
    font-family: 'Georgia';
    font-weight: 400;
    font-size: 35px;
    line-height: 130%;
    color: #FFFFFF;
    margin: 0;
    margin-bottom: 40px;

    @media(max-width:570px) {
      font-size: 25px;
      text-align: center;
    }
  }

  &__email--link,
  &__phone--link {
    font-family: 'Georgia';
    font-weight: 400;
    font-size: 20px;
    line-height: 115%;
    color: #F1CDB3;

  }

  &__mail {
    margin-bottom: 40px;
  }
}

.mail-img,
.phone-img {
  width: 40px;
  height: 32px;
  margin-right: 20px;

}

.loc-img {
  width: 22px;
  height: 32px;
  margin-right: 20px;
}

.footer__mail,
.footer__phone {
  display: flex;
  align-items: center;
  @media(max-width:570px) {
    justify-content: center;
  }
}
