.about {
  &__inner {
    display: flex;
    align-items: center;
    padding-bottom: 6.25rem;

    @media (max-width: 768.98px) {
      flex-wrap: wrap;
      justify-content: center;

    }
    @media (max-width: 620.98px) {
      padding-bottom: 5.7rem;

    }
    @media (max-width: 520.98px) {
      padding-bottom: 4.7rem;

    }
    @media (max-width: 420.98px) {
      padding-bottom: 3.7rem;

    }
    @media (max-width: 320.98px) {
      padding-bottom: 2.65rem;

    }
  }

  &__img {
    padding: 6.7% 10% 0px 14.2%;

    @media (max-width: 768.98px) {
      flex: 1 1 100%;
      display: flex;
      justify-content: center;
      padding: 0;
      order: 1;
    }

  }

  &-pets {
    max-width: 18.75rem;

    @media (max-width: 768.98px) {}
  }

  &__text {
    padding-top: 7.9%;
    max-width: 430px;

    @media (max-width: 768.98px) {
      padding: 11.3% 0px;
      flex: 1 1 100%;
    }
    @media (max-width: 520.98px) {
      padding: 12.3% 0px;

    }
    @media (max-width: 420.98px) {
      padding: 13.3% 0px;

    }
    @media (max-width: 320.98px) {
      padding: 14.3% 0px;

    }
  }

  &__title {
    margin: 0px 0px 25px 0px;
    font-family: 'Georgia';
    font-weight: 400;
    font-size: 2.1875rem;
    line-height: 130%;
    color: #545454;

    @media (max-width: 768.98px) {
      text-align: center;
    }

    @media (max-width: 520.98px) {
      font-size: 1.875rem;
    }

    @media (max-width: 320.98px) {
      font-size: 1.5625rem;
    }
  }

  &__title--text {
    margin: 0px 0px 0px 0px;
    font-family: 'Arial';
    font-weight: 400;
    font-size: 0.9375rem;
    line-height: 160%;
    color: #4C4C4C;

    @media (max-width: 768.98px) {
      text-align: justify;

    }
    @media (max-width: 450.98px) {
      padding: 0px 15px;

    }

    &:not(:last-child) {
      margin-bottom: 5.89%;
      @media (max-width: 768.98px) {
        margin-bottom: 5.89%;

      }
      @media (max-width: 620.98px) {
        margin-bottom: 5.6%;

      }
      @media (max-width: 420.98px) {
        margin-bottom: 6.4%;

      }
      @media (max-width: 320.98px) {
        margin-bottom: 7.4%;

      }
    }
  }
}
