.header {
  position: absolute;
  width: 100%;
  left: 0;
  top: 0;
  z-index: 10;
  padding-top: 3.75rem;
  color: #FAFAFA;

  @media(max-width:768.98px) {
    padding-top: 1.879rem;
  }

  &__wrapper {
    display: flex;
    overflow: hidden;
    justify-content: space-between;
    align-items: center;

    @media(max-width:768.98px) {
      justify-content: stretch;
    }
  }

  &__leftside {
    display: flex;
    flex-direction: column;
    z-index: 6;

    @media(max-width:768.98px) {
      margin-right: auto;
    }
  }

  &__logo {
    font-family: 'Georgia';
    font-style: normal;
    font-weight: 400;
    font-size: 2rem; // 32px
    line-height: 110%;
    letter-spacing: 0.06em;
    color: #F1CDB3;
    margin-bottom: 0.625rem; // 10px
  }

  &__logo--desqr {
    font-family: 'Arial';
    font-style: normal;
    margin: 0;
    letter-spacing: 0.1em;
    color: #FFFFFF;
    font-weight: 400;
    font-size: 0.8125rem;
    line-height: 93.75%;
    letter-spacing: 0.1em;
  }

  // OUR PETS
  &--ourTitle{
    color: #545454;
  }
  &--ourSubtitle{
    color: #292929;
  }
}
