.donate {
  background: #F6F6F6;
  padding: 5rem 0 6.25rem 0;

  @media (max-width: 768.98px) {
    padding: 2.625rem 0 2.625rem 0;
  }

  &__inner {
    display: flex;
    align-items: center;
    justify-content: center;

    @media (max-width: 991px) {
      flex-wrap: wrap;
    }
  }

  &__image {
    max-width: 100%;
    margin-right: 1.875rem;

    @media (max-width: 991px) {
      display: flex;
      justify-content: center;
      flex: 1 1 100%;
      margin: 0;
    }
  }

  &__bank {
    @media (max-width: 991px) {
      flex: 1 1 100%;
      order: -1;
      max-width: 380px;
      margin-bottom: 3.75rem;
    }

    @media (max-width: 570.98px) {
      text-align: center;
      max-width: none;
      margin-bottom: 2.625rem;
    }
  }

  &__title {
    margin: 0;
    font-family: 'Georgia';
    font-weight: 400;
    font-size: 2.1875rem;
    line-height: 130%;
    color: #545454;
    margin-bottom: 1.25rem;

    @media (max-width: 570.98px) {
      font-size: 1.5625rem;
    }
  }

  &__type {
    font-family: 'Georgia';
    font-weight: 400;
    font-size: 0.9375rem;
    line-height: 110%;
    color: #545454;
    margin: 0;
    margin-bottom: 1.25rem;
  }

  &__account {
    cursor: pointer;
    border: 3px solid #F1CDB3;
    display: flex;
    align-items: center;
    padding: 0.625rem 0.9375rem;
    background: #F1CDB3;
    border-radius: 0.5625rem;
    max-width: 350px;
    margin-bottom: 1.25rem;
    transition: all .3s ease-in;

    &:hover {
      background-color: inherit;

    }

    @media (max-width: 570.98px) {
      max-width: 17.5625rem;
      margin: 0 auto;
      margin-bottom: 1.25rem;
    }
  }

  &-svg {
    width: 30px;
    height: 23px;
    margin-right: 0.9375rem;
  }

  &__bank--account {
    font-family: 'Georgia';
    font-weight: 400;
    font-size: 1.25rem;
    line-height: 115%;
    color: #545454;

    @media (max-width: 570.98px) {
      font-size: 0.9375rem;
    }
  }

  &__desqr {
    max-width: 380px;
    font-family: 'Arial';
    font-style: italic;
    font-weight: 400;
    font-size: 0.75rem;
    line-height: 1.125rem;
    color: #B2B2B2;

    @media (max-width: 570.98px) {
      margin: 0 auto;
      max-width: 100%;
      text-align: justify;
    }
  }
}
