.nav {

  display: block;

  @media(max-width:768.98px) {
    // display: none;

    position: fixed;
    flex-direction: column;

    text-align: center;

    // height: 100vh;
    right: 0;
    left: 0;
    top: 0;
    bottom: 0;
    z-index: 3;
    margin-right: -150%;
    background: #292929;

    visibility: hidden;
    opacity: 0;
    transition: all 1s ease;
    // animation-name: menuBurger;
    // animation-duration: 1.5s;
    // animation-delay: 0s;
    // animation-fill-mode: forwards;
  }
}

.menu {
  color: #CDCDCD;
  display: flex;
  align-self: center;

  @media(max-width:768.98px) {
    display: flex;
    justify-content: flex-end;

  }

  @media(max-width:768.98px) {
    margin-top: 15.625rem;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  @media (max-width:768.98px) and (orientation:landscape) {
    margin-top: 6.625rem;
  }
  // @keyframes menuBurger {
  //   from {
  //     width: 0vw;
  //   }

  //   to {
  //     width: 100vw;
  //   }
  // }

  // .close-burger {
  //   animation-name: closeBurger;
  //   animation-duration: 0.9s;
  //   animation-delay: 0s;
  //   animation-fill-mode: forwards;
  // }

  // @keyframes closeBurger {
  //   from {
  //     width: 0vw;
  //   }

  //   to {
  //     width: 100vw;
  //   }
  // }



  &__list {
    cursor: pointer;
    border-bottom: 3px solid rgba(0, 0, 0, 0);
    padding-bottom: 0.5rem;

    transition: all .3s ease-in;

    &:not(:last-child) {
      margin-right: 35px;

      @media(max-width:768.98px) {
        margin-right: 2.1875rem;
      }

      @media (max-width: 768.98px) {
        margin-right: 0;

      }
    }

    &:hover,
    &:focus {
      border-bottom: 3px solid #F1CDB3;
      color: #FAFAFA;
    }
  }


  &__item {
    font-family: 'Arial';
    font-weight: 400;
    font-size: 0.9375rem;
    line-height: 160%;
    color: inherit;

    @media(max-width:768.98px) {
      font-size: 2rem;

    }
  }
  &__list--ourPets {

    &:hover,
    &:focus {

      color: #292929;
    }
  }

  &--ourPets {
    color: #545454;

    @media (max-width: 768.98px) {
      color: #fff;
    }
  }

}

.menu.active {
  padding-top: 13.625rem;

}
